
/* ==================== css-animations ======================= */

@keyframes scroll {
	0% {top: 0; opacity: 0;}
	50% {opacity: 1;}
	100% {top: 20px; opacity: 0;}
}


@keyframes opacity {
	0% {opacity: 0;}
	100% {opacity: 1;}
}

@keyframes move {
	0% {left: 100vw;}
	100% {left: -200vw;}
}